<template>
	<view class="notice-box" @click="jump" v-if="!isEmpty">
		<view class="notice-icon iconfont" :style="{'color':rdata.params.color}">&#xea80;</view>
		<view class="notice-text" :style="{'color':rdata.params.color}">{{ text }}</view>
	</view>
</template>

<script>
	export default {
		props: {
			rdata:{
				required: true,
			}
		},
		data(){
			return {
				text: "",
				isEmpty: false
			};
		},
		mounted(){
			if(this.rdata.params.list[0] == undefined){
				this.isEmpty = true;
			}else{
				this.text = this.rdata.params.list[0].title;
			}
		},
		methods: { 
			jump(){
				this.$utils.navigateTo('news/view',{ id: this.rdata.params.list[0].id });
			}
		}
	}
</script>

<style lang="scss" scoped>
	.notice-box { 
		padding: 0 30rpx;
		height: 60rpx;
		line-height: 60rpx;
		position: relative;
		.notice-icon {
			font-size: 33rpx;
			color: rgb(185, 25, 34);
			float: left;
			top: 1rpx;
			position: absolute;
		}
		.notice-text {
			font-size: 28rpx;
			text-indent: 45rpx;
			color: rgb(185, 25, 34);
			white-space:nowrap; overflow:hidden; text-overflow:ellipsis;
		}
	}
</style>
